@import './variable';
@import './mixin';

footer {
  background-color: #222;
}

.copy_right {
  margin: 0;
  font-weight: 700;
  font-size: 16px;
  color: #FFFFFF;
  text-align: center;
  padding: 20px 0;
}

.go_top {
  position: fixed;
  bottom: 70px;
  right: 25px;
  width: 40px;
  height: 40px;
  border-radius: 100%;
  background-color: #222;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  i {
    color: #FFFFFF;
  }
}