@import './variable';
@import './mixin';

header {
  background-color: #222;
}

.nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
  .logo {
    display: block;
    padding: 15px;
    color: #FFFFFF;
  }
}

.menu_list {
  display: flex;
  align-items: center;
  li {
    position: relative;
    a {
      display: block;
      font-weight: 700;
      font-size: 16px;
      color: #FFFFFF;
      padding: 20px;
      transition: all .3s;
      &:hover {
        background-color: lighten(#222, 10%);
      }
    }
    &:hover {
      > ul {
        display: block;
      }
    }
  }
}

.sub_list {
  display: none;
  position: absolute;
  top: 100%;
  left: -5px;
  width: 100px;
  background-color: #222;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 0px 0px 5px 5px;
  transition: .3s all;
  z-index: 2;
  li {
    text-align: center;
  }
}

.menu_toggle {
  display: none;
  padding: 30px 15px;
  transition: all .3s;
  cursor: pointer;
  span {
    display: block;
    width: 30px;
    height: 3px;
    background-color: #FFFFFF;
    position: relative;
    &:before, &:after {
      display: block;
      content: '';
      width: 30px;
      height: 3px;
      background-color: #FFFFFF;
      position: absolute;
    }
    &:before {
      top: -8px;
    }
    &:after {
      bottom: -8px;
    }
  }
  &.active {
    span {
      background-color: transparent;
      &:before {
        top: 0;
        transform: rotate(45deg);
      }
      &:after {
        bottom: 0;
        transform: rotate(-45deg);
      }
    }
  }
}

@include rwd-mobile(sm) {
  .menu_list {
    display: none;
    &.active {
      display: block;
      width: 100%;
      height: 100%;
      position: absolute;
      top: 63px;
      left: 0;
      z-index: 999;
      background-color: rgba(#000000, 0.4);
      li {
        text-align: center;
        background-color: lighten(#222, 5%);
        &:hover {
          > ul {
            display: none;
          }
        }
      }
    }
  }

  .menu_toggle {
    display: block;
  }
}