@import './variable';
@import './mixin';

.index_container {
  display: flex;
  margin: 0 -10px;
  padding: 30px 25px;
  > div {
    &:nth-child(1) {
      flex: 0 0 70%;
    }
    &:nth-child(2) {
      flex: 0 0 30%;
    }
    .gap {
      padding: 0 10px;
    }
  }
}

.tab_container {
  ul.tab_list {
    display: flex;
    align-items: center;
    li {
      flex: 0 0 25%;
      font-weight: 700;
      font-size: 16px;
      color: #FFFFFF;
      text-align: center;
      padding: 10px 0;
      background-color: #DCDCDC;
      border-radius: 5px 5px 0 0;
      cursor: pointer;
      transition: all .3s;
      &:hover, &.active {
        color: #000000;
        background-color: #FAFAD2;
      }
    }
  }
  .tab_body {
    border: 5px double #fafad2;
    border-radius: 0px 0px 10px 10px;
    padding: 30px;
    .tab_box {
      display: none;
      animation-name: fadeInOpacity;
      animation-iteration-count: 1;
      animation-timing-function: ease-in-out;
      animation-duration: .3s;
      > div {
        display: flex;
        img {
          width: 200px;
          height: 200px;
        }
        p {
          margin: 0;
          font-weight: 700;
          font-size: 20px;
          padding-left: 30px;
        }
      }
      &.active {
        display: block;
      }
    }
  }
}

.auto_container {
  margin-top: 30px;
  border: 5px double #FFC7A0;
  border-radius: 10px;
  padding: 30px;
  .auto_title {
    h2 {
      margin: 0;
      font-weight: 700;
      font-size: 20px;
      line-height: 24px;
      color: #F6906C;
      text-align: center;
      padding-bottom: 10px;
      border-bottom: 2px solid #F6906C;
    }
  }
  .auto_body {
    display: flex;
    flex-wrap: wrap;
    padding-top: 20px;
    margin: 0 -5px;
    > div {
      display: none;
      flex: 0 0 50%;
      padding: 10px 0;
      .gap {
        padding: 0 5px;
      }
    }
  }
}

.ranking_container {
  border: 5px double #A8E6E6;
  border-radius: 10px;
  padding: 20px;
  .ranking_title {
    h2 {
      margin: 0;
      font-weight: 700;
      font-size: 20px;
      line-height: 24px;
      color: #91CAC0;
      text-align: center;
      padding-bottom: 10px;
      border-bottom: 2px solid #91CAC0;
    } 
  }
  .ranking_list {
    li {
      padding: 10px 0;
      transition: all .3s;
      cursor: pointer;
      h3 {
        margin: 0;
        font-weight: 700;
        font-size: 20px;
      }
      img {
        display: none;
        padding-top: 10px;
        padding-left: 20px;
        animation-name: fadeInOpacity;
        animation-iteration-count: 1;
        animation-timing-function: ease-in-out;
        animation-duration: .3s;
      }
    }
  }
}

@include rwd-mobile(md) {
  .index_container {
    flex-direction: column;
    padding: 20px 10px;
  }

  .ranking_container {
    margin-top: 30px;
    .ranking_list {
      display: flex;
      flex-wrap: wrap;
      li {
        flex: 0 0 50%;
        display: flex;
        flex-direction: column;
        align-items: center;
        img {
          padding-top: 20px 10px 0;
        }
      }
    }
  }
}

@include rwd-mobile(xs) {
  .auto_container {
    padding: 15px;
    .auto_body {
      padding-top: 15px;
    }
  }

  .tab_container {
    .tab_body {
      padding: 15px;
      .tab_box {
        > div {
          img {
            width: 50%;
          }
          p {
            font-size: 16px;
            padding-left: 20px;
          }
        }
      }
    }
  }

  .ranking_container {
    .ranking_list {
      li {
        h3 {
          font-size: 16px;
        }
      }
    }
  }
}
