@import './variable';
@import './mixin';

.album_container {
  padding: 30px 0;
}

.album_block {
  &:first-child {
    margin-bottom: 50px;
    .album_body {
      padding: 30px 50px;
    }
  }
  h2 {
    margin: 0;
    margin-bottom: 20px;
    font-weight: 700;
    font-size: 26px;
    text-align: center;
  }
}

.album_body {
  border: 5px double #FAFAD2;
  border-radius: 10px;
  padding: 30px;
  .album_large {
    width: 100%;
    margin-bottom: 20px;
    img {
      width: 100%;
      border-radius: 10px;
    }
  }
  .album_list {
    display: flex;
    align-items: center;
    margin: 0 -10px;
    > div {
      flex: 0 0 100%;
      .gap {
        padding: 0 10px;
      }
    }
    img {
      border-radius: 10px;
      opacity: 0.7;
      cursor: pointer;
      transition: all .3s;
      &:hover {
        opacity: 1;
      }
    }
    button:before {
      color: #000000;
    }
  }
}

.album_block:nth-child(2) .album_content {
  display: flex;
  flex-wrap: wrap;
  margin: 0 -10px;
  > div {
    flex: 0 0 25%;
    .gap {
      padding: 0 10px;
    }
  }
  .album_box {
    cursor: pointer;
    > img {
      margin: 10px 0;
      border-radius: 10px;
      opacity: 0.7;
      transition: all .3s;
      &:hover {
        opacity: 1;
      }
    }
  }
}

@include rwd-mobile(sm) {
  .album_block {
    &:first-child {
      .album_body {
        padding: 20px;
      }
    }
  }

  .album_body {
    padding: 20px;
  }

  .album_block:nth-child(2) .album_content {
    > div {
      flex: 0 0 50%;
    }
  }
}

@include rwd-mobile(xs) {
  .album_block:nth-child(2) .album_content {
    > div {
      flex: 0 0 100%;
    }
  }
}