.custom_container {
  max-width: 1040px;
  margin: 0 auto;
  padding: 0 20px;
}

.main_section {
  flex: 1 1 auto;
  background-color: #FAFAD2;
}

.wrapper {
  height: 100%;
  background-color: #FFFFFF;
}

.banner_img {
  width: 100%;
  padding-bottom: 40%;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}

.radio_block {
  display: flex;
  align-items: center;
  padding: 4px 0;
  > div {
    margin-right: 8px;
  }
  label {
    padding-left: 24px;
    color: #2F4F4F;
    position: relative;
    cursor: pointer;
    input {
      opacity: 0;
      position: absolute;
      top: 0;
      left: 0;
    }
    .check_mark {
      position: absolute;
      top: 50%;
      left: 0;
      transform: translateY(-50%);
      width: 16px;
      height: 16px;
      border-radius: 16px;
      border: 1px solid #004231;
      &:after {
        display: none;
        content: '';
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 8px;
        height: 8px;
        border-radius: 8px;
        background: #619C87;
      }
    }
    input:checked ~ .check_mark {
      background-color: #FFFFFF;
    }
    input:checked ~ .check_mark:after {
      display: block;
    }
  }
}

@include rwd-mobile(xs) {
  .banner_container {
    margin: 0 -20px;
  }

  .banner_img {
    padding-bottom: 60%;
  }
}