@import './variable';
@import './mixin';

.member_container {
  padding: 30px 0;
  h2 {
    margin: 0;
    margin-bottom: 20px;
    font-weight: 700;
    font-size: 26px;
    text-align: center;
  }
}

.tool_container {
  display: flex;
  align-items: center;
  h4 {
    margin: 0;
    font-weight: 700;
    font-size: 16px;
    color: #004231;
    padding-right: 10px;
  }
}

.content_container {
  display: flex;
  margin: 0 -16px;
  flex-wrap: wrap;
  padding-top: 20px;
  > div {
    flex: 0 0 33.33%;
    margin-bottom: 20px;
    .gap {
      height: 100%;
      padding: 0 16px;
    }
  }
}

.card {
  height: 100%;
  border: 2px solid #619C87;
  padding: 8px;
  border-radius: 20px;
}

.card_box {
  height: 100%;
  border-radius: 16px;
  padding: 20px;
  background-color: #D9EDDF;
  text-align: center;
  img {
    width: 200px;
    border-radius: 20px;
  }
  h3 {
    min-height: 70px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 16px 0;
    font-weight: 700;
    font-size: 24px;
  }
}

.contact {
  text-align: left;
  li {
    display: flex;
    align-items: flex-start;
    padding: 4px 0;
    word-break: break-all;
    > span {
      span {
        display: block;
        width: 32px;
        height: 32px;
        background-color: #FFFFFF;
        border-radius: 32px;
        position: relative;
      }
      i {
        color: #004231;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
      }
    }
    a {
      display: inline-block;
      font-size: 14px;
      line-height: 32px;
      color: #2F4F4F;
      margin-left: 8px;
      &:hover {
        color: #609F9F;
      }
    }
  }
}

@include rwd-mobile(md) {
  .content_container {
    > div {
      flex: 0 0 50%;
    }
  }
}

@include rwd-mobile(xs) {
  .content_container {
    > div {
      flex: 0 0 100%;
    }
  }
}