@import './variable';
@import './mixin';

.modal_container {
  display: none;
  position: fixed;
  width: 100vw;
  height: 100vh;
  background-color: rgba(#000000, 0.8);
  z-index: 999;
  animation-name: fadeInOpacity;
  animation-iteration-count: 1;
	animation-timing-function: ease-in-out;
	animation-duration: .3s;
  .modal_content {
    width: 600px;
    height: 400px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
}

.modal_header {
  text-align: right;
  button {
    background-color: transparent;
    border: none;
    cursor: pointer;
    i {
      color: #FFFFFF;
    }
  }
}

.modal_body {
  img {
    width: 100%;
  }
}

@include rwd-mobile(sm) {
  .modal_container {
    .modal_content {
      width: 100%;
      height: auto;
    }
  }
}
