@import './variable';
@import './mixin';

.search_container {
  padding: 30px 0;
}

.search_title {
  display: flex;
  align-items: center;
  h2 {
    margin: 0;
    font-weight: 700;
    font-size: 26px;
    i {
      margin-right: 10px;
    }
  }
}

.search_box {
  display: flex;
  align-items: center;
  margin-left: 10px;
  i {
    color: #EB1025;
  }
  label {
    font-weight: 700;
    font-size: 20px;
    padding-right: 10px;
  }
}

.search_result {
  display: flex;
  flex-wrap: wrap;
  margin: 0 -10px;
  padding-top: 30px;
  > div {
    flex: 0 0 50%;
    margin-bottom: 20px;
    .gap {
      padding: 0 10px;
      height: 100%;
    }
  }
}

.search_info {
  border: 5px double #FAFAD2;
  border-radius: 10px;
  padding: 20px;
  height: 100%;
  h3 {
    margin: 0;
    font-weight: 700;
    font-size: 24px;
  }
  p {
    margin: 0;
    margin-bottom: 10px;
    font-size: 16px;
    display: flex;
    align-items: flex-start;
    &.detail {
      font-size: 14px;
      color: #9B9B9B;
      padding-top: 5px;
    }
    &.area, &.address, &.service, &.pay {
      &:before {
        display: block;
        content: '';
        font-family: 'Material Icons';
        font-size: 18px;
        padding-top: 5px;
        padding-right: 5px;
      }
    }
    &.area:before {
      content: '\ef3a';
      color: #EB1025;
    }
    &.address:before {
      content: '\e567';
      color: #00955A;
    }
    &.service:before {
      content: '\e8ae';
      color: #8D4CFF;
    }
    &.pay:before {
      content: '\f041';
      color: #226AFF;
    }
  }
}

@include rwd-mobile(xs) {
  .search_title {
    flex-direction: column;
  }

  .search_box {
    padding-top: 20px;
  }

  .search_result {
    > div {
      flex: 0 0 100%;
    }
  }
}