$break-ss: 479px;
$break-xs: 575px;
$break-sm: 767px;
$break-md: 991px;
$break-lg: 1199px;

@mixin rwd-mobile($media) {
  @if ($media == ss) {
    @media (max-width: $break-ss) {
      @content;
    }
  }
  @else if ($media == xs) {
    @media (max-width: $break-xs) {
      @content;
    }
  }
  @else if ($media == sm) {
    @media (max-width: $break-sm) {
      @content;
    }
  }
  @else if ($media == md) {
    @media (max-width: $break-md) {
      @content;
    }
  }
  @else if ($media == lg) {
    @media (max-width: $break-lg) {
      @content;
    }
  }
}